: none;\n    font-size: 20px;\n    cursor: pointer;\n}\n\n#nav i{\n    margin-right: 5px;\n    cursor: pointer;\n}\n\n/* UPLOAD */\n\n#div-upload{\n    width: 80%;\n    margin: 20px auto;\n    box-shadow: 0 0 6px 0 #767577;\n}\n\n#div-upload-2{\n    width: 80%;\n    margin: 20px auto;\n    box-shadow: 0 0 6px 0 #767577;\n}\n\n#upload{\n    padding: 20px;\n    width: 100%;\n    margin: 0 auto;\n    text-align: center;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\n#nameuser{\n    background: #251d71;\n    padding: 10px;\n    color: #fff;\n    text-align: center;\n}\n\n#formUploadFile, #formCreateDir{\n    display: flex;\n    justify-content: center;\n}\n\n#upload .inputs{\n    display: flex;\n}\n\n#nameFile{\n    display: flex;\n    padding: 5px;\n    border-left: 1px solid #251d71;\n}\n\n#upload .icon{\n    display: flex;\n    align-items: center;\n    position: relative;\n    height: 30px;\n    width: 30px;\n}\n\n#upload .icon input[type=\"submit\"]{\n    width: 30px;\n    height: 30px;\n    background: #251d7100;\n    z-index: 2;\n}\n\n#upload .icon i{\n    position: absolute;\n    left: 50%;\n    top: 59%;\n    transform: translate(-50%, -50%);\n    color: #fff;\n    cursor: pointer;\n    background: #251d71;\n    padding: 9px;\n    font-size: 12px;\n}\n\n#upload h3{\n    margin-bottom: 5px;\n}\n\n#upload-1{\n    margin-bottom: 15px;\n}\n\n#archivo{\n    display: none;\n}\n\nlabel[for=\"archivo\"]{\n    padding: 6.2px 25px;\n    font-size: 17px;\n    border-bottom: 1px solid #251d71;\n    cursor: pointer;\n}\n\n/* MENU DESPLEGABLE */\n\n.dropdown-container{\n    position: absolute;\n    top: 5px;\n    left: 10%;\n}\n\n.dropdown-container div{\n    font-size: 5px;\n    background: #dedede;\n    width: 23px;\n    height: 23px;\n    cursor: pointer;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    border-radius: 50%;\n}\n\n.dropdown-container ul{\n    height: 0px;\n    overflow: hidden;\n    transition: all .3s;\n    list-style: none;\n    box-shadow: 0 0 5px #989696;\n    width: 100px;\n}\n\n.dropdown-container ul li{\n    cursor: pointer;\n    padding: 5px 10px;\n}\n\n.dropdown-container ul li.ulClose{\n    background: #9e0101;\n    color: #fff;\n    text-align: center;\n    font-size: 20px;\n    padding: 0px;\n}\n\n.dropdown-container ul li:hover{\n    color: #fff;\n    background: #251d71;\n}\n\n.dropdown-container ul.show{\n    margin: 5px 20px;\n    height: auto;\n    background: #fff;\n    position: absolute;\n    top: -600%;\n    left: 10px;\n    z-index: 10;\n}\n\n/* ANTERIOR */\n\n#anterior{\n    display: flex;\n    justify-content: space-evenly;\n    padding: 5px;\n    text-align: center;\n    border-bottom: 1px solid #d193ff;\n    font-size: 20px;\n    color: #251d71;\n    transition: all 500ms;\n}\n\n#anterior i{\n    cursor: pointer;\n}\n\n#anterior i:hover{\n    color: #584fad;\n}\n\n#anterior div{\n    width: 4px;\n    background: #251d71;\n}\n\n/* MODAL */\n\n.modal{\n    width: 100%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    position: fixed;\n    z-index: 20;\n    top: 0;\n    left: 0;\n    transition: all 500ms ease;\n    opacity: 0;\n    visibility: hidden;\n}\n\n#btn-modal:checked ~ .modal{\n    opacity: 1;\n    visibility: visible;\n}\n\n.contenedor-modal{\n    width: 400px;\n    height: auto;\n    border-radius: 10px;\n    margin: auto;\n    background: #fff;\n    box-shadow: 1px 7px 25px rgba(0,0,0,0.6);\n    transition: all 500ms ease;\n    position: relative;\n    transform: translateY(-30%);\n}\n\n#btn-modal:checked ~ .modal .contenedor-modal{\n    transform: translateY(0);\n}\n\n.contenedor-modal .header{\n    padding: 10px;\n    background: #01015a;\n    color: #fff;\n    border-radius: 10px 10px 0 0;\n}\n\n.contenedor-modal label{\n    position: absolute;\n    top: 10px;\n    right: 10px;\n    color: #fff;\n    font-size: 15px;\n    cursor: pointer;\n}\n\n#btn-modal{\n    display: none;\n}\n\n.