idth: 5px;\n}\n\n#ficheros .fichero .texto::-webkit-scrollbar-thumb{\n    background: #01015a;\n    border-radius: 5px;\n}\n\n#carpetaVacia{\n    padding: 15px;\n    font-size: 20px;\n    text-align: center;\n}\n\n/* DETAILFILE */\n\n#detailFile{\n    width: 80%;\n    box-shadow: 0 0 6px #767577;\n    margin: 0 auto;\n}\n\n#detailFile .titleFile{\n    display: flex;\n    justify-content: space-between;\n    font-size: 18px;\n    background: #251d71;\n    color: #fff;\n    padding: 5px;\n}\n\n#detailFile .titleFile i{\n    font-size: 19px;\n    cursor: pointer;\n    margin-right: 2px;\n}\n\n#detailFile .detailFile{\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding: 5px 10px;\n}\n\n#detailFile .detailFile .textFile p{\n    font-weight: 600;\n}\n\n#detailFile .titleFile > p{\n    margin-left: 20px\n}\n\n#detailFile .detailFile .dowload i{\n    color: #ce0404;\n    font-size: 19px;\n    cursor: pointer;\n}\n\n@media(min-width: 425px){\n    #nav > h3{\n        font-size: 30px;\n    }\n}\n\n@media(min-width: 768px){\n    /* UPLOAD */\n    #upload{\n        flex-direction: row;\n        justify-content: space-evenly;\n    }\n    \n    #upload input[type=\"text\"]{\n        margin-bottom: 0;\n    }\n\n    #upload-1{\n        margin-bottom: 0;\n    }\n\n    #div-upload-2{\n        width: 80%;\n    }\n\n    /* FICHEROS */\n    #ficheros{\n        display: grid;\n        grid-template-columns: repeat(2, 1fr);\n        gap: 5px 20px;\n    }\n\n}\n\n@media(min-width: 1024px){\n    #div-upload{\n        width: 60%;\n    }\n\n    #div-upload-2{\n        width: 80%;\n    }\n\n    /* DETAILFILE */\n    #detailFile{\n        width: 60%;\n    }\n\n    /* MENU DESPLEGABLE */\n    .dropdown-container{\n        left: 12%;\n    }\n}\n\n@media(min-width: 1200px){\n    #div-upload{\n        width: 720px;\n    }\n\n    #div-upload-2{\n        width: 850px;\n    }\n\n    /* DETAILFILE */\n    #detailFile{\n        width: 720px;\n    }\n}\n/*# sour